.wrapper {
    min-height: calc(100vh - 179px);
    background-color: #eee;

    .wrapper__detail {
        background-color: #fff;
        padding: 15px 0;
    }

    .wrapper__main {
        padding: 15px 0;

        .wrapper__main__search {
            padding: 10px 0;
            border-bottom: 1px solid #ccc;

            .wrapper__main__search__title {
                color: #222;
                font-weight: 600;
                font-size: 20px;
                margin-bottom: 10px;
                position: relative;

                a {
                    position: absolute;
                    left: 0px;
                    font-size: 14px;
                    text-decoration: none;
                    cursor: pointer;
                    top: 8px;
                }
            }

            .wrapper__main__search__item {
                display: flex;
                align-items: flex-start;
                margin-bottom: 15px;

                .wrapper__main__search__item__title {
                    color: #222;
                    font-size: 16px;
                    font-weight: 600;
                }
                .wrapper__main__search__item__icon {
                    font-size: 30px;
                    margin-left: 10px;
                    width: 40px;
                }

                .wrapper__main__search__item__desc {
                    color: #666;
                    font-size: 16px;
                    font-weight: 500;
                }
            }
        }

        .wrapper__main__box {
            position: relative;
            padding: 10px;
            border-radius: 5px;
            background-color: #fff;
            box-shadow: 0px 0px 10px #ccc;
            margin-bottom: 20px;

            .wrapper__main__box__title {
                text-align: right;
                font-size: 16px;
                font-weight: 600;
                padding-bottom: 15px;
                border-bottom: 1px dotted #ccc;
                padding: 10px;
            }

            .wrapper__main__box__item {

                display: flex;
                align-items: center;
                padding-right: 40px;
                justify-content: space-around;

                @media(max-width:767px) {
                    padding-right: 20px;
                }

                .wrapper__main__box__item__title {
                    font-weight: 600;
                    font-size: 18px;
                    color: #000;
                    margin-right: 20px;

                    @media(max-width:767px) {
                        margin-right: 10px;
                        font-size: 16px;
                    }
                }

                img {
                    max-width: 60px;
                }

                .wrapper__main__box__item__price {
                    text-align: center;
                    border-right: 1px solid #ccc;
                    margin-right: 20px;
                    padding-right: 20px;

                    @media(max-width:767px) {
                        padding-right: 5px;
                        margin-right: 0;
                    }

                    p , h2 { 
                        margin: 0;
                    }

                    .wrapper__main__box__item__price__isgreen {
                        color: green;
                        font-size: 20px;
                        font-weight: 600;
                    }

                    .btn {
                        width:150px;
                        margin-top: 10px;

                        @media(max-width:767px) {
                            width:120px;
                        }
                    }
                }

                .wrapper__main__box__item__more {
                    position: absolute;
                    bottom: -12px;
                    right: calc(50% - 30px);
                    width:30px;
                    height:30px;
                    border: 1px solid green;
                    border-radius: 50%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                }
            }

            &.isSystem::before {
                content: 'سیستمی ';
                position: absolute;
                border: 1px solid #999;
                transform: rotate(270deg);
                right: -12px;
                border-bottom-color: #fff;
                padding: 2px 7px;
                border-radius: 5px 5px 0 0;
                font-size: 16px;
                color: #999;
                font-weight: 500;
                top: 36px;
            }

            .wrapper__main__box__detail {
                padding: 10px;
                border-top: 1px dotted #ccc;
                margin-top: 15px;
                display: flex;

                @media(max-width:767px) {
                    flex-direction: column;
                }
                
                .wrapper__main__box__detail__left {
                    padding: 5px;
                    margin-right: 10px;
                    width:280px;

                    @media(max-width:767px) {
                        width:100%;
                    }
                }
            }
        }
    }

    @media(max-width:991px) {
        .col-md-4.col-xs-12.pull-right {
            margin-top: 50px;
        }
    }
}

.wizard-progress {
    display: table;
    width: 100%;
    table-layout: fixed;
    position: relative;
    margin-bottom: 45px;

    .step {
        display: table-cell;
        text-align: center;
        vertical-align: top;
        overflow: visible;
        position: relative;
        font-size: 14px;
        color: #444;
        font-weight: bold;

        // &.in-progress {
        //     color: green;
        //     font-size: 16px;
        // }

        &::before {
            content: '';
            display: block;
            position: absolute;
            left: 0%;
            top: 37px;
            background-color: #ccc;
            height: 6px;
            width: 100%;
        }

        .node {
            display: inline-block;
            border: 6px solid #ccc;
            background-color: #ccc;
            border-radius: 18px;
            height: 18px;
            width: 18px;
            position: absolute;
            top: 30px;
            left: 50%;
            margin-left: -18px;
            z-index: 10;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;
            font-size: 11px;
        }

        &.complete {
            &:before {
                background-color: green;
            }

            .node {
                border-color: green;
                background-color: green;

                &:before {
                    font-family: FontAwesome;
                    content: "\f00c";
                }
            }
        }

    }
}

.wizard-progress .step.in-progress .node {
    border: 6px solid green;
    background-color: #ccc;
}

.flight {
    width: 200px;
    border-bottom: 1px dotted #ccc;
    margin-right: 20px;
    position: relative;

    @media(max-width:767px) {
        display: none;
    }

    &::before {
        position: absolute;
        content: '';
        width: 10px;
        height: 10px;
        border-radius: 50%;
        border: 1px solid green;
        top: -5px;
        left: -10px;
    }


    i {
        position: absolute;
        color: green;
        top: -8px;
        transform: rotate(225deg);
    }
}

.table thead tr th , .table tbody tr td {
    text-align: center;
}